.landingImg {
  height: 50vh;
  display: inline-block;
}

.landing {
	height: 100vh;
	width: 100vw;
	background-color: #EC4068;
	background: -webkit-linear-gradient(top left, #F45F55, #EC4068, #AE2E7A);
	background: -o-linear-gradient(top left, #F45F55, #EC4068, #AE2E7A);
	background: -webkit-gradient(linear, left top, right bottom, from(#F45F55), color-stop(#EC4068), to(#AE2E7A));
	background: linear-gradient(to bottom right, #F45F55, #EC4068, #AE2E7A);

	display: -ms-flexbox;

	display: flex;
  	-ms-flex-pack: center;
  	    justify-content: center;
  	-ms-flex-align: center;
  	    align-items: center;

}

.absolute-center {
  margin: auto;
  position: absolute;
  top: 0; left: 0; bottom: 0; right: 0;
}




